@import "../../index.scss";

$walimah-bg: url("../../../assets/img/Walimatul-Ursy.png");
$mempelai-bg: url("../../../assets/img/Mempelai.jpg");
$frame-bg: url("../../../assets/img/Frame-Square.png");

.Second-Page {
    .Walimah {
        width: 100%;
        text-align: center;

        .Caligraph {
            width: 100%;
            height: 50px;
            @include bg-img($walimah-bg);
            background-size: contain;
            margin-top: 20px;
        }

        .Foto-Frame-Container {
            display: flex;
            align-items: center;
            justify-content: center;

            .Foto-Mempelai {
                visibility: hidden;
                opacity: 0;
                height: 215px;
                width: 215px;
                position: relative;
                transform: translateX(0px);
                @include Default-Anim(0.5s, inherit);
                @include bg-img($mempelai-bg);
                background-size: contain;

                &:is(.Foto-Mempelai-Active) {
                    visibility: visible;
                    opacity: 1;
                    transform: translateX(90px);

                    animation-name: Foto-Mempelai-Anim;
                    @keyframes Foto-Mempelai-Anim {
                        0% {
                            visibility: hidden;
                            opacity: 0;
                            transform: translateX(0px);
                        }
                        100% {
                            visibility: visible;
                            opacity: 1;
                            transform: translateX(90px);
                        }
                    }
                }
            }

            .Frame-Mempelai {
                position: relative;
                visibility: hidden;
                opacity: 0;
                height: 250px;
                width: 250px;
                transform: translateX(-0);
                @include Default-Anim(0.5s, inherit);
                @include bg-img($frame-bg);
                background-size: contain;

                &:is(.Frame-Mempelai-Active) {
                    visibility: visible;
                    opacity: 1;
                    transform: translateX(-65px);

                    animation-name: Frame-Mempelai-Anim;
                    @keyframes Frame-Mempelai-Anim {
                        0% {
                            visibility: hidden;
                            opacity: 0;
                            transform: translateX(-0);
                        }
                        100% {
                            visibility: visible;
                            opacity: 1;
                            transform: translateX(-65px);
                        }
                    }
                }
            }
        }

        .Mempelai {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            // overflow: hidden;

            // MAKE ABSOLUTE CHILDERNS FOLLOWS THE PARENT SCROLL
            position: relative;

            .Nama {
                position: relative;
                visibility: hidden;
                opacity: 0;
                z-index: 1;
                transition: 0.5s all ease;
                font-size: 50px;
                font-family: Cultural;
                color: var(--blue);
                &:is(.Wanita) {
                    transform: translateX(500px);
                    &:is(.Nama-Wanita-Active) {
                        visibility: visible;
                        opacity: 1;
                        transform: translateX(-30px);
                    }
                }
                &:is(.Pria) {
                    margin-bottom: 20px;
                    transform: translateX(-500px);
                    &:is(.Pria-Active) {
                        visibility: visible;
                        opacity: 1;
                        transform: translateX(30px);
                    }
                }
            }

            .And {
                font-size: 20px;
                line-height: 20px;
                visibility: hidden;
                opacity: 0;
                position: relative;
                transform: translateY(-25px);
                font-family: TheConstellation;
                color: #c0c0c0;
                @include Default-Anim(1s, inherit);
                &:is(.And-Active) {
                    visibility: visible;
                    opacity: 1;
                    animation-name: And-Anim;
                    transform: translateY(-5px);
                    @keyframes And-Anim {
                        0% {
                            visibility: hidden;
                            opacity: 0;
                            transform: translateY(-25px);
                        }
                        100% {
                            visibility: visible;
                            opacity: 1;
                            transform: translateY(-5px);
                        }
                    }
                }
            }
        }

        .Akan-Melangsungkan {
            margin-top: 10px;
            font-size: 13px;
            line-height: 13px;
        }

        .Tanggal {
            font-size: 20px;
            line-height: 20px;
            margin-top: 10px;
            margin-bottom: 20px;
        }

        .Count-Down-Label {
            font-size: 13px;
            line-height: 13px;
        }

        .Count-Down {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 10px;

            .Count-Down-Section {
                width: 50px;
                height: 50px;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                background-color: var(--blue);
                border-radius: 50%;
                &:nth-child(n + 2) {
                    margin-left: 5px;
                }

                p {
                    &:not(.Label) {
                        font-size: 15px;
                        line-height: 20px;
                        margin-top: -5px;
                        color: #eee;
                        font-weight: bold;
                    }
                    &:is(.Label) {
                        line-height: 10px;
                        font-size: 10px;
                        color: #c4c4c4;
                    }
                }
            }
        }

        .Lagi {
            font-size: 13px;
            line-height: 13px;
            margin-top: 10px;
        }

        .Smiley-Container {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 50px;
            .Smiley {
                font-size: 0px;
                line-height: 20px;
                position: relative;
                z-index: 1;
                @include Default-Anim(0.3s, inherit);

                &:is(.Smiley-Active) {
                    font-size: 20px;
                    line-height: 20px;
                    animation-name: Smiley-Anim;
                    @keyframes Smiley-Anim {
                        0% {
                            font-size: 0px;
                            line-height: 20px;
                        }
                        70% {
                            font-size: 40px;
                            line-height: 40px;
                        }
                        100% {
                            font-size: 20px;
                            line-height: 20px;
                        }
                    }
                }
            }
        }
    }
}
