.Second-Page {
    .Resepsi {
        width: 100%;
        text-align: center;

        .Title {
            font-family: AdorableHandmade;
            font-size: 30px;
            letter-spacing: 1px;
            font-weight: bold;
            color: var(--blue);
            margin-bottom: 10px;
        }

        .Hari, .Jam {
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .Hari {
            font-size: 16px;
            line-height: 16px;
            margin-bottom: 5px;
        }

        .Jam {
            font-size: 16px;
            line-height: 16px;
        }
    }
}