@import "../../index.scss";
$bg-jazakumullah: url("../../../assets/img/jazakumullah.png");

.Second-Page {
    .Lainnya {
        width: 100%;
        text-align: center;

        .Jazakumullah {
            width: 100%;
            height: 150px;
            @include bg-img($bg-jazakumullah);
            background-size: contain;
        }

        .Ucapan {
            font-size: 15px;
        }

        .Title {
            font-family: AdorableHandmade;
            font-size: 30px;
            letter-spacing: 1px;
            font-weight: bold;
            color: var(--blue);
            // margin-bottom: 10px;
        }

        .Hormat {
            font-size: 15px;
        }

        .Hormat-Wanita {
            font-family: Cultural;
            font-size: 30px;
        }

        .Hormat-Dan {
            font-size: 17px;
        }

        .Hormat-Pria {
            font-family: Cultural;
            font-size: 30px;
        }
    }
}
