@import "../../index.scss";

$bismillah-img: url("../../../assets/img/Bismillah.png");
$foto-wanita: url("../../../assets/img/Mempelai-Wanita.jpg");
$foto-pria: url("../../../assets/img/Mempelai-Pria.jpg");
$frame-bg: url("../../../assets/img/Frame-Round-1.png");

.Second-Page {
    .Intro {
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        text-align: center;

        .Bismillah {
            width: 100%;
            height: 70px;
            @include bg-img($bismillah-img);
            background-size: contain;
            margin-bottom: 20px;
        }

        .Assalamualaikum {
            font-weight: bold;
            font-size: 15px;
        }

        .Segala-Puji {
            font-size: 14px;
        }

        .Ar-Rum-21 {
            font-size: 15px;
        }

        .Mengharap-Ridho {
            font-size: 15px;
        }

        .Mempelai {
            width: 100%;
            margin-top: 30px;

            .Wanita,
            .Pria {
                width: 100%;
                position: relative;
                display: flex;
                align-items: center;
                flex-direction: column;

                .Foto {
                    width: 130px;
                    height: 130px;
                    border-radius: 50%;
                    position: relative;
                    visibility: hidden;
                    opacity: 0;
                    @include Default-Anim(0.5s, inherit);

                    &:is(.Wanita .Foto) {
                        @include bg-img($foto-wanita);
                        transform: rotate(-50deg);
                        &:is(.Foto-Active) {
                            visibility: visible;
                            opacity: 1;
                            transform: rotate(-0deg);
                            animation-name: Foto-Wanita-Anim;
                            @keyframes Foto-Wanita-Anim {
                                0% {
                                    visibility: hidden;
                                    opacity: 0;
                                    transform: rotate(-50deg);
                                }
                                100% {
                                    visibility: visible;
                                    opacity: 1;
                                    transform: rotate(-0deg);
                                }
                            }
                        }
                    }

                    &:is(.Pria .Foto) {
                        @include bg-img($foto-pria);
                        transform: rotate(50deg);
                        &:is(.Foto-Active) {
                            visibility: visible;
                            opacity: 1;
                            transform: rotate(0deg);
                            animation-name: Foto-Pria-Anim;
                            @keyframes Foto-Pria-Anim {
                                0% {
                                    visibility: hidden;
                                    opacity: 0;
                                    transform: rotate(50deg);
                                }
                                100% {
                                    visibility: visible;
                                    opacity: 1;
                                    transform: rotate(0deg);
                                }
                            }
                        }
                    }
                }

                .Frame {
                    width: 160px;
                    height: 160px;
                    position: absolute;
                    z-index: -1;
                    visibility: hidden;
                    opacity: 0;
                    @include Default-Anim(0.5s, inherit);
                    @include bg-img($frame-bg);

                    &:is(.Wanita .Frame) {
                        transform: translateY(-17px) translateX(-12px)
                            rotate(20deg);
                        &:is(.Frame-Active) {
                            visibility: visible;
                            opacity: 1;
                            z-index: 1;
                            transform: translateY(-17px) translateX(-12px)
                                rotate(90deg);
                            animation-name: Frame-Wanita-Anim;
                            @keyframes Frame-Wanita-Anim {
                                0% {
                                    visibility: hidden;
                                    opacity: 0;
                                    transform: translateY(-17px)
                                        translateX(-12px) rotate(20deg);
                                }
                                100% {
                                    visibility: visible;
                                    opacity: 1;
                                    transform: translateY(-17px)
                                        translateX(-12px) rotate(90deg);
                                }
                            }
                        }
                    }

                    &:is(.Pria .Frame) {
                        transform: translateY(-13px) translateX(10px)
                            rotate(-50deg);
                        &:is(.Frame-Active) {
                            visibility: visible;
                            opacity: 1;
                            z-index: 1;
                            transform: translateY(-13px) translateX(10px)
                                rotate(-90deg);
                            animation-name: Frame-Pria-Anim;
                            @keyframes Frame-Pria-Anim {
                                0% {
                                    visibility: hidden;
                                    opacity: 0;
                                    transform: translateY(-13px)
                                        translateX(10px) rotate(-50deg);
                                }
                                100% {
                                    visibility: visible;
                                    opacity: 1;
                                    transform: translateY(-13px)
                                        translateX(10px) rotate(-90deg);
                                }
                            }
                        }
                    }
                }

                .Nama {
                    font-family: Cultural;
                    text-align: center;
                    font-size: 30px;
                    position: relative;
                    margin-top: 20px;
                    color: #fff;
                    span {
                        font-family: Cultural;
                        opacity: 0;
                        transform: translateY(20px) translateX(10px);
                        display: inline-block;
                        color: #fff;
                        @include Default-Anim(0.5s, inherit);
                        &:is(.spacer) {
                            display: inline-block;
                            width: 5px;
                        }
                        &:is(.fade) {
                            opacity: 1;
                            transform: translateY(0px) translateX(0px);
                            color: var(--blue);
                            animation-name: Fade-Anim;
                            @keyframes Fade-Anim {
                                0% {
                                    opacity: 0;
                                    color: #fff;
                                    transform: translateY(20px) translateX(10px);
                                }
                                30% {
                                    color: #37e3f0;
                                    transform: translateY(-10px)
                                        translateX(10px);
                                }
                                80% {
                                    transform: translateY(10px) translateX(-5px);
                                }
                                100% {
                                    color: #a327a3;
                                    opacity: 1;
                                    transform: translateY(0px) translateX(0px);
                                }
                            }
                        }
                    }
                }

                .Orangtua {
                    visibility: hidden;
                    opacity: 0;
                    transform: translateY(50px);
                    transition: 0.3s all ease;
                    font-size: 15px;
                    &:is(.Orangtua-Active) {
                        visibility: visible;
                        opacity: 1;
                        transform: translateY(0px);
                    }

                    .Alamat {
                        font-size: 25px;
                        letter-spacing: 1px;
                        font-family: CampanaScript;
                        color: var(--blue);
                    }
                }
            }

            .Spacer {
                height: 2px;
                width: 0px;
                background-color: var(--blue);
                margin: 30px 0px 40px 0px;
                visibility: hidden;
                opacity: 0;
                position: relative;
                left: 50%;
                transform: translateX(-50%);

                @include Default-Anim(1s, inherit);

                &:is(.Spacer-Active) {
                    visibility: visible;
                    opacity: 1;
                    width: 100%;
                    animation-name: Spacer-Anim;
                    @keyframes Spacer-Anim {
                        0% {
                            width: 0px;
                            visibility: hidden;
                            opacity: 0;
                        }
                        100% {
                            visibility: visible;
                            opacity: 1;
                            width: 100%;
                        }
                    }
                }
            }
        }

        .Bermaksud {
            font-size: 15px;
        }
    }
}
