@import "../../index.scss";

$bg-left: url("../../../assets/img/Spacer-Left.png");
$bg-right: url("../../../assets/img/Spacer-Right.png");
$bg-center: url("../../../assets/img/Spacer-Center.png");
$bg-handle: url("../../../assets/img/Spacer-Handle.png");

.Second-Page {
    .Spacer-Bg {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        .Spacer-Left {
            visibility: hidden;
            opacity: 0;
            width: 30px;
            height: 30px;
            @include bg-img($bg-left);
            background-size: contain;
            @include Default-Anim(0.5s, inherit);
            position: relative;
            transform: rotate(-180deg);

            &:is(.Spacer-Left-Active) {
                visibility: visible;
                opacity: 1;
                transform: rotate(-0deg);
                animation-name: Spacer-Left-Anim;
                @keyframes Spacer-Left-Anim {
                    0% {
                        visibility: hidden;
                        opacity: 0;
                        transform: rotate(-180deg);
                    }
                    100% {
                        visibility: visible;
                        opacity: 1;
                        transform: rotate(-0deg);
                    }
                }
            }
        }

        .Spacer-Center {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            height: 20px;
            padding: 0px 5px 0px 5px;

            .Center {
                width: 0px;
                visibility: hidden;
                opacity: 0;
                border-radius: 2px;
                height: 4px;
                @include bg-img($bg-center);
                transition: 0.5s all ease;

                &:is(.Center-Active) {
                    width: 100%;
                    visibility: visible;
                    opacity: 1;
                }
            }
            .Handle {
                height: 15px;
                width: 0px;
                visibility: hidden;
                opacity: 0;
                position: absolute;
                @include bg-img($bg-handle);
                background-size: contain;
                transition: 0.5s all ease;

                &:is(.Handle-Active) {
                    width: 30%;
                    visibility: visible;
                    opacity: 1;
                }
            }
        }

        .Spacer-Right {
            visibility: hidden;
            opacity: 0;
            width: 30px;
            height: 30px;
            @include bg-img($bg-right);
            background-size: contain;
            @include Default-Anim(0.5s, inherit);
            position: relative;
            transform: rotate(180deg);

            &:is(.Spacer-Right-Active) {
                visibility: visible;
                opacity: 1;
                transform: rotate(0deg);
                animation-name: Spacer-Right-Anim;
                @keyframes Spacer-Right-Anim {
                    0% {
                        visibility: hidden;
                        opacity: 0;
                        transform: rotate(180deg);
                    }
                    100% {
                        visibility: visible;
                        opacity: 1;
                        transform: rotate(0deg);
                    }
                }
            }
        }
    }
}
