.Second-Page {
    .Footer {
        width: 100%;
        text-align: center;

        .Dibuat-Oleh {
            font-size: 13px;

            .Heart {
                margin-top: 7px;
                margin-bottom: 10px;
            }
        }

        .Pengantin {
            font-size: 30px;
            font-family: CampanaScript;
        }

        .Social-Media {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;

            div {
                display: flex;
                align-items: center;
                justify-content: center;
                height: 35px;
                width: 35px;
                border-radius: 4px;
                background-color: #fff;
                -webkit-box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.59);
                -moz-box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.59);
                box-shadow: 0px 1px 11px -1px rgba(0, 0, 0, 0.59);
            }

            .Instagram, .Facebook {
                margin-left: 7px;
            }
        }

        .Copyright {
            font-size: 12px;
            color: var(--light-grey);
            position: relative;
            transform: translateY(50px);
        }
    }
}
