@import "../../index.scss";

$bg-insya-allah: url("../../../assets/img/InsyaAllah.png");

.Second-Page {
    .Akad {
        width: 100%;
        text-align: center;

        .InsyaAllah {
            width: 100%;
            height: 130px;
            @include bg-img($bg-insya-allah);
            background-size: contain;
        }

        .Title {
            font-family: AdorableHandmade;
            font-size: 30px;
            letter-spacing: 1px;
            font-weight: bold;
            color: var(--blue);
            margin-bottom: 10px;
        }

        .Hari, .Jam {
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .Hari {
            font-size: 16px;
            line-height: 16px;
            margin-bottom: 5px;
        }

        .Jam {
            font-size: 16px;
            line-height: 16px;
        }
    }
}