.Second-Page {
    .Maps {
        width: 100%;
        text-align: center;

        .Title {
            font-family: AdorableHandmade;
            font-size: 30px;
            letter-spacing: 1px;
            font-weight: bold;
            color: var(--blue);
        }

        .Alamat {
            font-size: 15px;
        }

        .OpenMap {
            border: none;
            font-size: 14px;
            padding: 7px 10px 7px 10px;
            background-color: var(--blue);
            color: #eee;
            border-radius: 4px;
        }

        .Mohon-Maaf {
            font-size: 14px;
        }
    }
}