.RBX-Loading {
    width: 100%;
    height: 100vh;
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 10;
    // background-color: rgba($color: #000000, $alpha: .5);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .Loading-Text {
        width: 100%;
        text-align: center;
        .Head {
            font-size: 40px;
            letter-spacing: 2px;
            font-family: TheConstellation;
            color: #fff;
            span {
                font-family: TheConstellation;
                opacity: 0;
                transition: 0.5s all ease;
                transform: translateY(50px);
                display: inline-block;
                color: rgb(0, 204, 255);
                &:is(.spacer) {
                    display: inline-block;
                    width: 5px;
                }
                &:is(.fade) {
                    opacity: 1;
                    transform: translateY(0px);
                    color: var(--blue);
                }
            }
        }
        .Wait {
            font-size: 13px;
        }
    }
}